<template>
  <div id="search">
    <md-card md-with-hover>
      <md-card-header> Search Open Datasets</md-card-header>
      <md-card-actions>
        <md-input-container>
          <label>Search</label>
          <md-input placeholder="Search by type, title or description etc. " v-model="searchquery" id="searchquery">
          </md-input>
          <a href="#" v-on:click="search(searchquery)">
            <md-icon class="md-size-1x md-accent">search</md-icon>
          </a>
        </md-input-container>
      </md-card-actions>
    </md-card>
    <br>
    <p v-if="fetchStatus != null">{{fetchStatus}}</p>
    <p v-if="hasResults">{{displayWhenHasResults()}}</p>
    <p v-if="noResult && isSearchComplete && fetchStatus==null">
      <md-icon class="md-size-1x md-warn">help</md-icon> search returned no result please try another query</p>
    <p v-if="!searchButtonclicked || searchquery===null">
      <md-icon class="md-size-1x md-warn">help_outline</md-icon>
      {{searchButtonclickedOrSearchqueryNullDisplay()}}
    </p>
    <md-spinner :md-size="60" md-indeterminate class="md-warn" v-if="searchButtonclicked && !isSearchComplete && fetchStatus===null"></md-spinner>
  <p v-if="hasResults">{{response.hits.hits[0].recallID}}</p>
   <recallListResults v-if="hasResults" :response="response">
   </recallListResults>
   <neissListResults v-if="hasResults" :response="response">
   </neissListResults>
   
  
  </div>
</template>


<script>
import recallListResults from "./recalls/RecallListResults.vue";
import neissListResults from "./neiss/NeissListResults.vue";
//import searchListResultsContainer from "./SearchListResultsContainer";
//import searchResultsRefiner from './SearchResultsRefiner'

import _ from "lodash";
import { eventBus } from "../main";

export default {
  name: "search",
  components: {
    //searchListResultsContainer
    //searchResultsRefiner,
    recallListResults,
    neissListResults
  },
  data: function() {
    return {
      stats: {
        took: Number,
        total: Number,
        maxScore: Number
      },
      searchSettings: {
        size: 10,
        from: Number,
        dataType: String
      },
      dataContainer: {
        recalls: [],
        incidentReports: [],
        neiss: [],
        violations: []
      },
      aggregations: {
        artifactType: String,
        artifactSource: String
      },
      response: null,
      dataTypes: [],
      searchquery: null,
      hasResults: false,
      resultCount: 0,
      isSearchComplete: false,
      searchButtonclicked: false,
      fetchStatus: null,
      responseEvent: null,
      wasComponentUpdated: false
    };
  },
  watch: {
    searchquery: function(newQuery) {
      var that = this;
      that.fetchStatus = "Waiting for input...";
      that.search(newQuery);
    }
  },
  computed: {
    noResult: function() {
      return (
        this.searchButtonclicked && !this.hasResults && this.searchquery != null
      );
    }
  },

  methods: {
    displayWhenHasResults: function() {
      return (
        "Found: " +
        this.stats.total +
        " records in " +
        this.stats.took +
        " ms,Max Relevance Score : " +
        this.stats.maxscore +
        ", Displaying top: " +
        this.resultCount +
        " results containing " +
        this.searchquery
      );
    },

    searchButtonclickedOrSearchqueryNullDisplay: function() {
      return this.searchquery
        ? "Query=" + this.searchquery
        : "Please enter a query";
    },
    resetFormItems: function() {
      var that = this;
      that.searchButtonclicked = true;
      that.isSearchComplete = false;
      that.hasResults = false;
      that.resultCount = 0;
      that.dataContainer.recalls = [];
      that.dataContainer.neiss = [];
      that.fetchStatus = null;
    },

    filterResponseData:(response)=> {
      var that = this;
      console.log('in the filterResponseData method, the value of response.hits is'+ response.hits.hits[0].recallID)
      that.hasResults = true//response.hits.total > 0 ? true : false;
      that.resultCount = that.searchSettings.size;
      //eventBus.$emit('newSearchRequested', response)
    },

    search: _.debounce(
      function(searchquery) {
        var that = this;
        that.resetFormItems();

        var urlRoot = "/search";
        //TODO: just switch no local node js api server, need to finish code to create a request object to pass down to the api.
        //Also code to parse the api response
        if (searchquery.length == 0 || searchquery.length === null) {
          that.isSearchComplete = true;
          return;
        }
        that.$http
          .post(urlRoot, {
            FullText: this.searchquery,
            Filter: {
              Source: "cpsc",
              Type: "",
              StartDate: "01-01-1970",
              EndDate: "01-01-2018"
            },
            StartPage: "1",
            NumPerPage: that.searchSettings.size
          })
          .then(function(response) {
            console.log(response.status);
            console.log(response.statusText);
          
           var responseData = response.data.results;
           
            that.stats.took = responseData.took;
            that.stats.total = responseData.hits.total;
            that.stats.maxscore = responseData.hits.max_score;
            that.responseEvent = responseData;
            that.response = responseData;
            that.filterResponseData(response);

            that.isSearchComplete = true;
          })
          .catch(function(error) {
            console.log("could not reach API " + error);
            this.fetchStatus =
              "Communication with the back end server failed...please try again later";
          });
      },
      // number of milliseconds to wait for user to finish typing
      450
    )
  }
};
</script>



